@import '../../../theme/palette.scss';

.layout {
  min-height: calc(100vh - 5rem);
  width: 60%;
  background-color: $secondary;
  box-sizing: border-box;
}

@media (max-width: 1580px) {
  .layout {
    width: 80%;
  }
}

@media (max-width: 1024px) {
  .layout {
    width: 100%;
  }
}
